import {Pasttrial} from '@/api/user'
import {MpvuePicker} from 'components/uni/mpvue-picker/mpvue-picker'
export default {
	components:{
		MpvuePicker
	},
	data(){
		return{
			content:'',
			fontNum:500,
			remainingNum:500,
			state:{},
			themeColor: '#007AFF',
			pickerValueDefault:[1],
			pickerValueArray:[
				{
					label: '建议反馈',
			        value: 0,
				},{
					label: '违法违规举报',
			        value: 1,
			}],
			pickerText:{
				label:'建议反馈',
				value:[0]
			},
		}
	},
	onLoad: function() {
		this.$store.ready(state=>{
			this.state = state
		})
	},
	
	methods: {
		onConfirm(e) {
		    this.pickerText = e
		},
		conInput(e){
			this.content = e.detail.value
			this.remainingNum = this.fontNum-this.content.length;
		},

		showPicker(){
			this.$refs.mpvuePicker.show()
		},
		submit(){
		
			if(this.content){
				uni.showLoading({
					title: '正在提交中'
				});
				Pasttrial({
					content:this.content
				}).then(res=>{
					uni.hideLoading();
					if(res.result){
						uni.showToast({
							title:'提交成功',
							duration: 2000,
							icon:'none'
						});	
					}else{
						uni.showToast({
							title: res.msg,
							duration: 2000,
							icon:'none'
						});
					}
				})
				
			}else{
				uni.showToast({
				    title: '请输入内容',
				    duration: 2000,
					icon:'none'
				});
			}
		}	
	}
}
